import { Injectable, OnDestroy } from '@angular/core';
import { MetricsBase } from './metrics-base';
import { AuthService, DataService } from '@front-shared-lib';

@Injectable({
  providedIn: 'root'
})
export class WebMetricsService
  extends MetricsBase
  implements OnDestroy {
  constructor(
    protected override authService: AuthService,
    protected override dataService: DataService
  ) {
    super(authService, dataService);
  }

  ngOnDestroy(): void {
    this.disconnect();
    this.destroy$.next();
    this.destroy$.complete();
  }
}


