import { AfterViewInit, Component, OnDestroy, OnInit } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Meta, Title } from '@angular/platform-browser';
import { FrontendLogger } from '@biz-nest/core-client';
import { AppNameService, AuthService, MainBarTitleService } from '@front-shared-lib';
import { EagleInfoClientError } from '@main-shared-lib';
import { Observable, Subject, takeUntil } from 'rxjs';
import { ISystemInfoResponse } from '../../../../services/metrics-base';
import { WebMetricsService } from '../../../../services/web-metrics.service';
import { AdminBreadcrumbComponent } from '../../misc/admin-breadcrumb/admin-breadcrumb.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';

// eslint-disable-next-line @typescript-eslint/naming-convention
declare let ApexCharts;

@Component({
  selector: 'app-web-droplet',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    AdminPageContentComponent,
    AdminBreadcrumbComponent
  ],
  templateUrl: './web-droplet.component.html',
  styleUrls: ['./web-droplet.component.scss']
})
export class WebDropletComponent
implements OnInit, OnDestroy, AfterViewInit {
  systemMetrics$: Observable<ISystemInfoResponse | null>;
  socketErrors$: Observable<string>;
  private destroy$ = new Subject<void>();
  private cpuChart;
  private memoryChart;
  private networkChart;
  private loadChart;
  isLoading = true;
  lastUpdated: Date | null = null;
  chartsReady = false;


  constructor(
    private authService: AuthService,
    private webMetricsService: WebMetricsService,
    private metaTagService: Meta,
    private titleService: Title,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService
  ) {
    this.systemMetrics$ = this.webMetricsService.systemMetrics$;
    this.socketErrors$ = this.webMetricsService.socketError$;
  }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Web Droplet - Droplet Metrics';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);

    this.metaTagService.updateTag({
      name: 'description',
      content: 'Monitor and analyze system metrics including CPU, memory, network, and load averages.'
    });

    if (!this.authService.webMetricsServer) {
      throw new EagleInfoClientError('No web metrics server found');
    }

    this.subscribeToMetrics();
    this.webMetricsService.connect(this.authService.webMetricsServer);
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.initializeCharts();
      this.chartsReady = true;
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    if (this.cpuChart) {
      this.cpuChart.destroy();
    }
    if (this.memoryChart) {
      this.memoryChart.destroy();
    }
    if (this.networkChart) {
      this.networkChart.destroy();
    }
    if (this.loadChart) {
      this.loadChart.destroy();
    }
  }

  private subscribeToMetrics(): void {
    this.systemMetrics$
      .pipe(takeUntil(this.destroy$))
      .subscribe(metrics => {
        if (metrics?.node) {
          this.lastUpdated = new Date();
          this.updateCharts(metrics.node);
          this.isLoading = false;
        }
      });

    this.socketErrors$
      .pipe(takeUntil(this.destroy$))
      .subscribe(error => {
        FrontendLogger.error('WebSocket error:', error);
        // Handle error state in UI
      });
  }

  private initializeCharts(): void {
    this.initializeCpuChart();
    this.initializeMemoryChart();
    this.initializeNetworkChart();
    this.initializeLoadChart();
  }

  private initializeCpuChart(): void {
    const options = {
      series: [0],
      chart: {
        height: 350,
        type: 'radialBar',
        offsetY: -10
      },
      plotOptions: {
        radialBar: {
          startAngle: -135,
          endAngle: 135,
          dataLabels: {
            name: {
              fontSize: '16px',
              color: undefined,
              offsetY: 120
            },
            value: {
              offsetY: 76,
              fontSize: '22px',
              color: undefined,
              formatter: function(val) {
                return val + '%';
              }
            }
          }
        }
      },
      fill: {
        type: 'gradient',
        gradient: {
          shade: 'dark',
          shadeIntensity: 0.15,
          inverseColors: false,
          opacityFrom: 1,
          opacityTo: 1,
          stops: [0, 50, 65, 91]
        }
      },
      stroke: {
        dashArray: 4
      },
      labels: ['CPU Usage'],
      colors: ['#088F8F', '#2196F3', '#FF9800']
    };

    this.cpuChart = new ApexCharts(document.querySelector('#cpu-chart'), options);
    this.cpuChart?.render();
  }

  private updateCharts(metrics): void {
    this.updateCpuChart(metrics.cpuUsagePercent);
    this.updateMemoryChart(metrics.memoryUsagePercent, metrics.memoryTotalBytes);
    this.updateNetworkChart(metrics.networkBytesReceivedPerSecond, metrics.networkBytesTransmittedPerSecond);
    this.updateLoadChart(metrics.loadAverage1m, metrics.loadAverage5m, metrics.loadAverage15m);
  }

  private updateCpuChart(usage: number): void {
    FrontendLogger.debug('Updating CPU chart with usage:', usage);
    this.cpuChart?.updateSeries([usage ? Number(usage.toFixed(2)) : 0]);
  }

  private initializeMemoryChart(): void {
    const options = {
      series: [0],
      chart: {
        height: 350,
        type: 'radialBar'
      },
      plotOptions: {
        radialBar: {
          startAngle: -135,
          endAngle: 135,
          hollow: {
            margin: 0,
            size: '70%'
          },
          dataLabels: {
            name: {
              offsetY: -10,
              color: '#333',
              fontSize: '13px'
            },
            value: {
              color: '#111',
              fontSize: '30px',
              show: true,
              formatter: function(val) {
                return val + '%';
              }
            }
          },
          track: {
            background: '#e0e0e0',
            strokeWidth: '97%',
            margin: 5
          }
        }
      },
      fill: {
        type: 'gradient',
        gradient: {
          shade: 'dark',
          shadeIntensity: 0.15,
          inverseColors: false,
          opacityFrom: 1,
          opacityTo: 1
        }
      },
      stroke: {
        lineCap: 'round'
      },
      labels: ['Memory Usage'],
      colors: ['#0F52BA', '#2196F3', '#FF9800']
    };

    this.memoryChart = new ApexCharts(document.querySelector('#memory-chart'), options);
    this.memoryChart?.render();
  }

  private updateMemoryChart(usage: number, totalBytes: number): void {
    const usedGB = totalBytes ? (totalBytes * (usage / 100) / (1024 ** 3)).toFixed(2) : 0;
    const totalGB = totalBytes ? (totalBytes / (1024 ** 3)).toFixed(2) : 0;

    this.memoryChart?.updateOptions({
      plotOptions: {
        radialBar: {
          dataLabels: {
            value: {
              formatter: function() {
                return usedGB + ' / ' + totalGB + ' GB';
              }
            }
          }
        }
      }
    });

    this.memoryChart?.updateSeries([usage || 0]);
  }

  private initializeNetworkChart(): void {
    const options = {
      series: [{
        name: 'Received',
        data: [0]
      }, {
        name: 'Transmitted',
        data: [0]
      }],
      chart: {
        type: 'bar',
        height: 350,
        stacked: true,
        toolbar: {
          show: false
        }
      },
      plotOptions: {
        bar: {
          horizontal: false,
          columnWidth: '55%',
          endingShape: 'rounded'
        }
      },
      dataLabels: {
        enabled: false
      },
      stroke: {
        show: true,
        width: 2,
        colors: ['transparent']
      },
      xaxis: {
        categories: ['Network']
      },
      yaxis: {
        title: {
          text: 'Bytes/s'
        }
      },
      fill: {
        opacity: 1
      },
      tooltip: {
        y: {
          formatter: function(val) {
            return val + ' bytes/s';
          }
        }
      },
      colors: ['#4CAF50', '#2196F3', '#FF9800']
    };

    this.networkChart = new ApexCharts(document.querySelector('#network-chart'), options);
    this.networkChart?.render();
  }

  private updateNetworkChart(received: number, transmitted: number): void {
    this.networkChart?.updateSeries([{
      name: 'Received',
      data: [received || 0]
    }, {
      name: 'Transmitted',
      data: [transmitted || 0]
    }]);
  }

  private initializeLoadChart(): void {
    const options = {
      series: [{
        name: 'Load Average',
        data: [0, 0, 0]
      }],
      chart: {
        height: 350,
        type: 'bar',
        toolbar: {
          show: false
        }
      },
      plotOptions: {
        bar: {
          borderRadius: 4,
          horizontal: true
        }
      },
      dataLabels: {
        enabled: false
      },
      xaxis: {
        categories: ['1 min', '5 min', '15 min'],
        title: {
          text: 'Load Average'
        }
      },
      yaxis: {
        labels: {
          show: false
        }
      },
      fill: {
        opacity: 1
      },
      tooltip: {
        y: {
          formatter: function(val) {
            return val;
          }
        }
      },
      colors: ['#87CEEB', '#008080', '#FF9800']
    };

    this.loadChart = new ApexCharts(document.querySelector('#load-chart'), options);
    this.loadChart?.render();
  }

  private updateLoadChart(load1m: number, load5m: number, load15m: number): void {
    this.loadChart?.updateSeries([{
      data: [
        load1m || 0,
        load5m || 0,
        load15m || 0
      ]
    }]);
  }

  formatBytes(bytes: number, decimals = 2): string {
    if (bytes === 0) {
      return '0 Bytes';
    }

    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

    const i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }
}
